
import { KeyboardArrowDownRounded,ShoppingCartRounded,MenuRounded,CloseRounded } from "@mui/icons-material";
import { useState } from "react";
import LoginWidget from "../widget/LoginWidget";
import Cart from "../widget/Cart";
import { useMediaQuery } from "@mui/material";



const NavBar=()=>{

    const isMobile = useMediaQuery('(min-width:768px)');
  const [cart,setCart]=useState(false);
  const [toggleMenu,setToggleMenu] = useState(false)


    return(
        <nav className="w-[100%] border-b-2 border-gray-300 ">
        <div className="flex w-[90%] mx-auto xs:max-md:justify-between flex-row">
            <div className="Logo basis-[10%]  ">
                 <p onClick={()=>{window.location.href='https://danial117.github.io/Info.vit/'}} className="text-[2rem] cursor-pointer md:max-lg:text-center md:max-lg:my-2 md:max-lg:text-[1.6rem]">Info<span>.vit</span></p>

            </div>
           {isMobile && <div className="flex text-center my-auto justify-center   className='font-Abel text-lg' mx-auto gap-x-16 md:max-lg:gap-x-4 basis-[70%] md:max-lg:basis-[80%] mx-auto flex-row">
                <p onClick={()=>{window.location.href='https://danial117.github.io/Info.vit/'}} className='font-Lexend cursor-pointer md:max-lg:text-[14px] text-lg'>Home <span><KeyboardArrowDownRounded/></span></p>
                <p onClick={()=>{window.location.href='https://danial117.github.io/about/'}} className='font-Lexend cursor-pointer md:max-lg:text-[14px] text-lg'>About <span><KeyboardArrowDownRounded/></span></p>
                <p className='font-Lexend md:max-lg:text-[14px] text-lg'>News <span><KeyboardArrowDownRounded/></span></p>
                <p className='font-Lexend md:max-lg:text-[14px] text-lg'>Info <span><KeyboardArrowDownRounded/></span></p>
                <p className='font-Lexend md:max-lg:text-[14px] text-lg'>Contact Us <span><KeyboardArrowDownRounded/></span></p>
            </div>}

          {isMobile &&  <div className="basis-[20%] md:max-lg:basis-[20%] md:max-lg:justify-end md:max-lg:gap-x-4  w-full gap-x-6 flex flex-row py-2 mx-auto">
                <ShoppingCartRounded onClick={()=>setCart(!cart)} style={{cursor:'pointer'}}/>
                <button onClick={()=>{window.location.href='https://danial117.github.io/signUp/'}} className="px-4  md:max-lg:text-[12px] md:max-lg:px-2  text-emerald-600 border-2 border-emerald-300  rounded-md text-sm  font-Poppins">
                    Sign Up
                </button>

            </div>}

            { !isMobile &&
            <div className="my-auto">
                 
                 <MenuRounded onClick={()=>{setToggleMenu(!toggleMenu)}}  style={{cursor:'pointer'}}/>

                </div>

            }

           
              { toggleMenu &&
              
              <div className="w-[30%] h-[100vh] right-0 z-[999] fixed bg-white"> 
                <div className="absolute text-emerald-500 right-[3%] top-[3%]">
                  <CloseRounded onClick={()=>{setToggleMenu(!toggleMenu)}} style={{fontSize:'40px',cursor:'pointer'}}/>
                </div>
                   <div className="flex w-[80%] text-white mx-auto gap-y-6 mt-24 text-black flex-col ">
                    <p onClick={()=>{window.location.href='https://danial117.github.io/Info.vit/'}} className="font-Abel border-2 bg-emerald-500 cursor-pointer  text-center p-2 border-emerald-500">Home</p>
                    <p onClick={()=>{window.location.href=''}} className="font-Abel border-2 bg-emerald-500 cursor-pointer text-center p-2 border-emerald-500">Info</p>
                    <p onClick={()=>{window.location.href='https://danial117.github.io/about/'}} className="font-Abel border-2 bg-emerald-500 cursor-pointer text-center p-2 border-emerald-500">About</p>
                    <p onClick={()=>{window.location.href='https://danial117.github.io/signUp/'}} className="font-Abel border-2 bg-emerald-500 cursor-pointer  text-center p-2 border-emerald-500">Sign Up</p>
                    <p onClick={()=>{window.location.href=''}} className="font-Abel border-2 bg-emerald-500 cursor-pointer text-center p-2 border-emerald-500">News</p>

                   </div>

                    </div>}
            

        </div>




        
    

      {cart &&  <Cart />}




        






    </nav>
    )
}



export default NavBar;







